export default {
  // 超出截断
  cutStr (val, length) {
    return val.length <= length ? val : val.substr(0, length)
  },
  // 溢出显示省略号
  subStr (val, length) {
    return val.length <= length ? val : val.substr(0, length) + '…'
  },
  // 金额千分位格式化
  numFormat (num) {
    return (num + '').replace(/\d{1,3}(?=(\d{3})+(\.\d*)?$)/g, '$&,')
  },
  //货币符号
	currency (val,type) {
		return (val === '' || val === null) ?  '--' : (type||'￥') +' ' + val
	},
	//百分比
	percentage (val,place) {
    return (val === '' || val === null) ? (place || '--') : val + '%'
	},
	//占位符
	placeholder (val,place) {
		return (val === '' || val === null) ? (place || '--') : val
  }
}


